<template>
  <div>
    <LayoutBox :one="$route.query.userId ? '编辑区级管理账号' : '添加区级管理账号'">
      <el-form ref="form" label-width="80px" :model="form.user" :rules="rules" size="small" label-position="left">
        <el-form-item label="用户账号" prop="loginName">
          <el-input v-model="form.user.loginName" class="input_width"></el-input>
        </el-form-item>
        <el-form-item label="用户密码" prop="password">
          <el-input ref="password" v-model="form.user.password" class="input_width" :type="type" onfocus="this.type='password'" autocomplete="new-password" clearable @blur="onBlur"></el-input>
        </el-form-item>
        <el-form-item label="用户昵称">
          <el-input v-model="form.user.name" class="input_width"></el-input>
        </el-form-item>
        <el-form-item label="用户邮箱">
          <el-input v-model="form.user.email" class="input_width"></el-input>
        </el-form-item>
        <el-form-item label="手机号码">
          <el-input v-model="form.user.mobile" class="input_width"></el-input>
        </el-form-item>
        <el-form-item label="用户性别">
          <el-radio v-model="form.user.gender" label="0">男</el-radio>
          <el-radio v-model="form.user.gender" label="1">女</el-radio>
          <el-radio v-model="form.user.gender" label="2">其他</el-radio>
        </el-form-item>
        <el-form-item v-for="(item, i) in form.list" :key="i" label="地区" required>
          <div class="area">
            <el-cascader v-model="form.list[i].areaCmp" :options="positionJson" @change="handleChange($event, i)"></el-cascader>
            <span class="iconfont icon_jian ml-1" @click="onDeleteArea(i)">&#xe678;</span>
          </div>
          <el-form v-for="(item1, i1) in item.list" :key="i1" size="small" inline>
            <el-form-item>
              <span class="iconfont icon_jian" @click="onDeleteCategory(i, i1)">&#xe678;</span>
            </el-form-item>
            <el-form-item label="分类" required>
              <el-select v-model="item1.categoryId" placeholder="请选择分类" @change="onCategory($event, i, i1)">
                <el-option v-for="itemCategory in categoryList" :key="itemCategory.id" :label="itemCategory.categoryName" :value="itemCategory.id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="提成" required>
              <el-input v-model="item1.percent" placeholder="请输入提成" style="width: 215px"></el-input>
              <span class="note">注：5%就输入0.5</span>
            </el-form-item>
            <el-form-item v-if="item.list.length-1 === i1">
              <el-button type="warning" @click="onAddCategory(i)">添加分类</el-button>
            </el-form-item>
          </el-form>
        </el-form-item>
        <el-form-item>
          <el-button type="success" @click="onAddArea">添加地区</el-button>
        </el-form-item>
        <el-form-item>
          <el-button v-if="$route.query.userId" type="primary" @click="onSave('form')">保存</el-button>
          <el-button v-else type="primary" @click="onSubmit('form')">提交</el-button>
          <el-button @click="$router.back(-1)">取消</el-button>
        </el-form-item>
      </el-form>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'
import positionJson from '@/assets/json/china_city_data_copy.json'

export default {
  name: 'admin_district_user_add',

  components: {
    LayoutBox,
  },

  data () {
    return {
      // 添加表单
      form: {
        user: {
          loginName: '', // 用户账号
          password: '', // 密码
        },
        list: [
          {
            area: '',
            areaCmp: [],
            list: [
              { 'categoryId': '' }
            ]
          }
        ]
      },
      rules: {
        loginName: [
          { required: true, message: '请输入用户账号', trigger: 'blur' },
          { min: 6, max: 18, message: '长度在 6 到 18 个字符', trigger: 'change' },
        ],
        password: [
          { required: true, message: '请输入用户密码', trigger: 'blur' },
          { min: 6, max: 64, message: '长度在 6 到 32 个字符', trigger: 'change' },
        ]
      },
      userPageNo: 1, // 用户表分页
      userList: [], // 用户列表
      categoryList: [], // 分类列表
      loading: false, // 查询时的loading
      positionJson: positionJson,
      type: 'text', // 为了禁用谷歌浏览器的表单自动填写密码
    }
  },

  methods: {
    // 焦点离开密码时
    onBlur () {
      if (!this.form.user.password) {
        this.type = 'text'
      }
    },
    // 选择地区
    handleChange(e, i) {
      if (!this._selectArea(e, i)) { return false }
      if (!e) { return false }
      let positionItem = this.positionJson.filter((item) => item.value === e[0])
      this.form.list[i].area = positionItem[0].label
      positionItem = positionItem[0].children.filter((item) => item.value === e[1])
      this.form.list[i].area = this.form.list[i].area + ',' + positionItem[0].label
      positionItem = positionItem[0].children.filter((item) => item.value === e[2])
      this.form.list[i].area = this.form.list[i].area + ',' + positionItem[0].label
    },
    // 添加地区
    onAddArea () {
      if (this._rulesList()) {
        this.form.list.push({
          area: '', list: [{ 'categoryId': '' }]
        })
      }
    },
    // 删除地区
    onDeleteArea (i) {
      this.form.list.splice(i, 1)
    },
    // 选择分类
    onCategory (e, i, i1) {
      this.apiCategoryFindById(e).then((res) => {
        this.form.list[i].list[i1].categoryName = res
      })
    },
    // 点击添加分类
    onAddCategory (i) {
      if (this._rulesList()) {
        this.form.list[i].list.push({ 'categoryId': '' })
      }
    },
    // 删除分类
    onDeleteCategory (i, i1) {
      this.form.list[i].list.splice(i1, 1)
    },
    // 点击提交
    onSubmit (formName) {
      console.log(this.form)
      if (this._rulesForm(formName)) {
        this.apiAddAreauser()
      }
    },
    // 点击保存
    onSave (formName) {
      if (this._rulesForm(formName)) {
        this.apiAreauserUpdate()
      }
    },

    // 私有方法-------------------------------------------------
    // 验证地区是否选了一样的
    _selectArea (e, i) {
      let isArea = true
      this.form.list.forEach((element, index) => {
        if (element.areaCmp.toString() === e.toString() && index !== i) {
          this.$message.error(`【${element.area}】已存在`)
          this.form.list[i].areaCmp = ''
          this.form.list[i].area = ''
          isArea = false
        }
      })
      return isArea
    },
    // 地区转换地区ID
    _getAreaId (area) {
      let areaArr = area.split(',')
      let areaGroup = []
      let positionItem = this.positionJson.filter((item) => item.label === areaArr[0])
      areaGroup.push(positionItem[0].value)
      positionItem = positionItem[0].children.filter((item) => item.label === areaArr[1])
      areaGroup.push(positionItem[0].value)
      positionItem = positionItem[0].children.filter((item) => item.label === areaArr[2])
      areaGroup.push(positionItem[0].value)
      return areaGroup
    },
    // 表单验证
    _rulesForm (formName) {
      let rulesForm = false
      if (!this._rulesList()) {
        return false
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {
          rulesForm = true
        }
      })
      return rulesForm
    },
    // 验证数组是否有空
    _rulesList () {
      for (let key in this.form.list) {
        if (!this.form.list[key].area) {
          this.$message.error(`请先选择第${Number(key) + 1}个地区`)
          return false
        }
        for (let key1 in this.form.list[key].list) {
          if (!this.form.list[key].list[key1].categoryId) {
            this.$message.error(`请先选择【${this.form.list[key].area}】下面的分类`)
            return false
          }
          if (!this.form.list[key].list[key1].percent) {
            this.$message.error(`请先选择【${this.form.list[key].area}】下面【${this.form.list[key].list[key1].categoryName}】的提成`)
            return false
          }
        }
      }
      return true
    },

    // api ----------------------------------
    // 分类分页查询
    apiCategoryQuery () {
      this.$http.post('/woho/product/category/findByTree')
        .then((res) => {
          if (res.data.success) {
            res.data.data.map((item) => {
              this.categoryList = [...this.categoryList, ...item.trees]
            })
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 根据主键id查询分类
    apiCategoryFindById (id) {
      return this.$http.post(`/woho/product/category/findById/${id}`)
        .then((res) => res.data.categoryName)
    },
    // 新增区级代理
    apiAddAreauser () {
      let data = {
        ...this.form,
      }
      this.$http.post('/woho/areauser/insert', data)
        .then((res) => {
          if (res.data.success) {
            this.$message.success('添加成功')
            this.$router.go(-1)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 编辑时回显根据userId查询
    apiAreauserFindByUserId () {
      this.$http.get(`/woho/areauser/findByUserId/${this.$route.query.userId}`)
        .then((res) => {
          if (res.data.success) {
            res.data.data.list.map((item) => {
              item.areaCmp = this._getAreaId(item.area)
            })
            this.form = res.data.data
            console.log('this.$refs(password)', this.$refs.password)
            this.$refs.password.focus()
            // this.type = 'password'
          } else {
            this.$message.error(res.data.msg)
          }
        })
    },
    // 修改区级代理
    apiAreauserUpdate () {
      let data = JSON.parse(JSON.stringify(this.form))
      if (data.user.password.length > 32) {
        data.user.password = ''
      }
      this.$http.put('/woho/areauser/update', data)
        .then((res) => {
          console.log(res)
          if (res.data.success) {
            this.$message.success('保存成功')
            this.$router.go(-1)
          } else {
            this.$message.error(res.data.msg)
          }
        })
    }
  },

  created () {
    this.apiCategoryQuery()
    if (this.$route.query.userId) {
      this.apiAreauserFindByUserId()
    }
  }
}
</script>

<style lang="scss" scoped>
  .input_width{
    width: 215px
  }
  .area{
    display: flex;
  }
  .note{
    font-size: 10px;
    margin-left: 5px;
  }

  .icon_jian{
    color: red;
    cursor: pointer;
  }
</style>
